import React, { useEffect } from "react";
import { Form, Icon, Input, Button, Row, Col, Typography, message } from "antd";
import { Link } from "react-router-dom";
import MyModal from "../../util/WarnModal";
import { connect } from "react-redux";
import { forgetPass } from "../../../action/auth";
import { clearErr } from "../../../action/resource";

const Singnin = props => {
  const { err } = props;
  useEffect(() => {
    if (Object.keys(props.err).length) {
      message.error("new props");
      MyModal({ data: props.err });
      props.clearErr();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [err]);

  const handleSubmit = e => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      //   if (!err) {
      props.forgetPass(values);
      // }
    });
  };

  const { getFieldDecorator } = props.form;
  return (
    <Row type="flex" justify="center">
      <Col
        style={{
          margin: "0 auto ",
          padding: "32px",
          borderRadius: "8px",
          border: "2px solid #e2e2e2"
        }}
        span={18}
      >
        <Form onSubmit={handleSubmit} className="login-form">
          <Typography.Title level={2} type="secondary">
            Forget Password Code
          </Typography.Title>
          <Form.Item>
            {getFieldDecorator("email", {
              rules: [
                {
                  type: "email",
                  message: "The input is not valid E-mail!"
                },
                {
                  required: true,
                  message: "Please input your E-mail!"
                }
              ]
            })(
              <Input
                type="email"
                prefix={
                  <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                }
                placeholder="email"
              />
            )}
          </Form.Item>
          <Form.Item>
            have validate code <Link to="/ChangePassword">change password</Link>
            <br />
            <Button
              type="primary"
              htmlType="submit"
              className="login-form-button"
              loading={props.loading}
            >
              Send validate Code
            </Button>
          </Form.Item>
        </Form>
      </Col>
    </Row>
  );
};

const mapStateToProps = state => ({
  auth: state.auth,
  err: state.err,
  loading: state.loading
});

const Signin = Form.create({ name: "signin_form" })(
  connect(
    mapStateToProps,
    { forgetPass, clearErr }
  )(Singnin)
);

export default Signin;
