import { Modal } from "antd";
import React from "react";

const WarnModal = ({ data }) => {
  const innerItems = array =>
    array.map((oneErr, index) => <li key={`lis + ${index}`}>{oneErr}</li>);
  const errsArray = Object.keys(data);
  let code;
  try {
    code = errsArray.map((err, index) => {
      const oneField = data[err];
      if (typeof oneField === "string") {
        return (
          <ul key="1">
            <li>
              {err}
              <ul key="2">{oneField}</ul>
            </li>
          </ul>
        );
      }
      return (
        <ul key={`uls + ${index}`}>
          <li>
            {err}
            <ul>{innerItems(oneField)}</ul>
          </li>
        </ul>
      );
    });
  } catch (error) {
    code = "server Error";
  }

  Modal.error({
    title: "Err :(",
    content: errsArray.length ? code : "",
    onOk() {}
  });
};

export default WarnModal;
